@media (min-width: 1200px) {
  .price__item {
    padding-top: 16px;
    padding-bottom: 22px;
    font-size: 18px;
  }
}
.price__item:nth-of-type(2) {
  border-left: none;
}
@media (min-width: 660px) {
  .price__item--empty {
    background-color: #F2F2F2;
    border-color: #F2F2F2;
    border-bottom: 2px solid #E5E5E5;
  }
}
.price__value {
  box-sizing: border-box;
  position: relative;
  padding: 13px 10px 16px 19px;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  border: 2px solid #E5E5E5;
  border-top: none;
}
@media (min-width: 660px) {
  .price__value {
    min-width: 155px;
    padding: 7px 0 7px 15px;
    text-transform: none;
  }
}
@media (min-width: 1200px) {
  .price__value {
    min-width: 235px;
    padding: 14px 0 14px 20px;
    font-size: 18px;
  }
}
.price__value--title {
  display: none;
}
@media (min-width: 660px) {
  .price__value--title {
    display: table-cell;
  }
}
@media (min-width: 1200px) {
  .price__value--title {
    display: table-cell;
  }
}
.price__rate {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 28px;
  text-transform: uppercase;
}
.price__rate--sale::before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 74px;
  height: 74px;
  background-image: url("../img/hit-ribbon.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 660px) {
  .price__rate--sale::before {
    width: 61px;
    height: 61px;
  }
}
@media (min-width: 1200px) {
  .price__rate--sale::before {
    width: 74px;
    height: 74px;
  }
}
.price__rate--sale::after {
  content: "Хит";
  position: absolute;
  top: 9px;
  left: 5px;
  width: 74px;
  height: 74px;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
  .price__rate--sale::after {
    top: 1px;
    left: 0;
  }
}
@media (min-width: 1200px) {
  .price__rate--sale::after {
    top: 10px;
    left: 5px;
  }
}
.price__caption-price {
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
}
@media (min-width: 1200px) {
  .price__caption-price {
    font-size: 24px;
  }
}
.price__value-mobile {
  text-transform: uppercase;
}
@media (min-width: 660px) {
  .price__value-mobile {
    display: none;
  }
}
.price__toggles {
  margin: 316px 0 0 0;
}
@media (min-width: 660px) {
  .price__toggles {
    margin-top: 0;
    display: none;
  }
}
.price__toggle {
  margin-left: 5px;
  margin-right: 5px;
  padding: 0;
}
.price__value--yes::before {
  content: " ";
  position: absolute;
  top: 19px;
  left: 228px;
  width: 31px;
  height: 21px;
  background-image: url("../img/yes.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 660px) {
  .price__value--yes::before {
    top: 12px;
    left: 60px;
  }
}
@media (min-width: 1200px) {
  .price__value--yes::before {
    top: 19px;
    left: 101px;
  }
}
.price__value--no::before {
  content: " ";
  position: absolute;
  top: 15px;
  left: 230px;
  width: 27px;
  height: 27px;
  background-image: url("../img/no.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 660px) {
  .price__value--no::before {
    top: 9px;
    left: 62px;
  }
}
@media (min-width: 1200px) {
  .price__value--no::before {
    top: 16px;
    left: 103px;
  }
}
.social__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.social__item:not(:last-of-type) {
  margin-right: 7px;
}
@media (min-width: 1200px) {
  .social__item:not(:last-of-type) {
    margin-right: 3px;
  }
}
.social__link {
  display: flex;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid rgba(40, 54, 69, 0.1);
}
.social__link:hover {
  border: 3px solid #283645;
}
.social__link:active {
  opacity: 0.1;
}
.social__img {
  margin: auto;
}
.social__img--twitter {
  width: 18px;
  height: 14px;
}
.social__img--facebook {
  width: 8px;
  height: 18px;
}
.social__img--youtube {
  width: 15px;
  height: 13px;
}
